
import React, { Component } from 'react';
import WidgetO from '../Widgets/Widget02'
// Main Chart
import ApiDashboard from '../../api/dashboard';
import {
  Card, CardBody,
  CardHeader,
  CardColumns
} from 'reactstrap';
import Loading from '../components/loading'

import { Bar, Doughnut, Line, Pie, Polar, Radar } from 'react-chartjs-2';

import { CustomTooltips } from '@coreui/coreui-plugin-chartjs-custom-tooltips';

const bar = {
  labels: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'],
  datasets: [
    {
      label: 'My First dataset',
      backgroundColor: 'rgba(17,168,141)',
      borderColor: 'rgba(255,99,132,1)',
      borderWidth: 1,
      hoverBackgroundColor: 'rgba(168,132,8,66)',
      hoverBorderColor: 'rgba(255,99,132,1)',
      data: [1500000, 1100000, 800000, 950000, 1120000, 700000, 800000, 1500000, 540000, 550000, 950000, 800000],
    },
  ],
};

const options = {
  tooltips: {
    enabled: false,
    custom: CustomTooltips
  },
  maintainAspectRatio: false
}

class Dashboard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      gastoTotal: '',
      mes: [],
      loading: true,

    }
  }
  componentDidMount() {
    this.getExpensesMonths()
    this.getExpensesMonth()

  }

  //getExpenseMont
  getExpensesMonth() {
    ApiDashboard.getTotalExpensesMonth()
      .then((response) => {
        this.setState({
          gastoTotal: response,
          loading: false
        });
      })
      .catch(e => console.log(e))
  }
  //getExpenseMonts
  getExpensesMonths() {
    ApiDashboard.getTotalExpensesMonths()
      .then((response) => {
        this.setState({
          mes: response,
          loading: false
        });
      })
      .catch(e => console.log(e))
  }


  render() {
    let mes_nombre = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre',]
    if (this.state.loading) {
      return (
        <Loading loading={this.state.loading}></Loading>
      );
    }
    return (
      <>
        <div className="animated fadeIn">
          <WidgetO
            header={'$' + new Intl.NumberFormat().format(this.state.gastoTotal.toString())}
            mainText='Gastos totales de este mes'
          />
        </div>
        <div className="animated fadeIn">
          <div>
            <div className="row">
              {this.state.mes.map(
                (item, index) =>
                  <div className="col-sm" key={index}>
                    <Card>
                      <CardHeader className="bg-primary">
                        <h4> {mes_nombre[item.mes - 1]} {item.anio} </h4>
                      </CardHeader>
                      <CardBody className="bg-info text-dark">
                        Gastos: <h3> {`$ ${new Intl.NumberFormat().format(item.precio_total_mes)}`} </h3>
                        <hr></hr>
                      </CardBody>
                      {/* <CardFooter>
                          <Button type="submit" size="sm" color="primary"><i className="fa fa-dot-circle-o"></i> Imprimir Gastos</Button>
                        </CardFooter> */}
                    </Card>
                  </div>
              )
              }

            </div>
          </div>
        </div>

        <div className="animated fadeIn">
          <CardColumns className="cols-2">

            <Card>
              <CardHeader>
                Bar Chart
              <div className="card-header-actions">
                  <a href="http://www.chartjs.org" className="card-header-action">
                    <small className="text-muted">docs</small>
                  </a>
                </div>
              </CardHeader>
              <CardBody>
                <div className="chart-wrapper">
                  <Bar data={bar} options={options} />
                </div>
              </CardBody>
            </Card>
          </CardColumns>
        </div>
      </>
    );
  }

}

export default Dashboard;